.Input {
  font-family: Roboto, sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 20px;

  label {
    position: relative;
    display: block;
    margin: 0 0 10px;
    color: rgba(0, 0, 0, 0.6);
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: .2em;
    order: 1;
  }

  input {
    outline: none;
    display: block;
    background: rgba(0, 0, 0, 0.1);
    width: 100%;
    border: 0;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px;
    color: rgba(0, 0, 0, 0.6);
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;
    order: 3;
  }

  span {
    font-size: 12px;
    font-weight: 700;
    order: 2;
    margin-bottom: 5px;
  }

  &.invalid label {
    color: #f01f30;
  }

  .required {
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translateY(-50%);
    font-size: 20px;
    line-height: 13px;
    color: red;
  }
}

.date {
  margin: 0;

  input {
    background: transparent;
    padding: 0;
  }

  input::-webkit-calendar-picker-indicator {
    margin-right: 15px;
    margin-left: 0;
  }

  input[type=date]::-webkit-inner-spin-button,
  input[type=date]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 10px;
  }
}